.content{width:100%;display:flex;justify-content:space-evenly;align-items:center;gap:40px}.image{aspect-ratio:16/9;width:640px;background:#6e6e6e;border-radius:15px;border:3px solid #f5f5f5;box-shadow:0 0 5px rgba(0,0,0,.2)}.info{display:flex;flex-direction:column;font-size:24px;gap:40px}.specs,.prices{display:flex;flex-direction:row;gap:20px}.specs{flex-direction:column}.item{border-radius:10px;display:flex;gap:5px;justify-content:baseline;text-align:left;font-weight:bold;background:rgba(0,0,0,0);color:#fff}.item::before{content:"•";margin-right:10px}.animal{font-weight:lighter !important}.price{width:100%;padding:5px 25px;border-radius:10px;border:3px solid #f5f5f5;font-weight:bold;background:#f5f5f5;color:#34a853;display:flex;flex-direction:column;gap:10px;justify-content:space-between;box-shadow:0 0 5px rgba(0,0,0,.2);align-items:center}.button{background:rgba(245,245,245,.1450980392);font-size:30px;max-width:400px;padding:10px 20px;transition:.2s all cubic-bezier(0.215, 0.61, 0.355, 1);border:3px solid #f5f5f5;border-radius:30px;font-weight:normal}.button:hover{background:#f5f5f5;border:3px solid #34a853;color:#34a853}@media screen and (max-width: 1280px){.image{width:100%}.content{flex-direction:column}.prices{flex-direction:column}.info{align-items:center;flex-direction:column}}